import { IsString, IsEmail } from 'class-validator';

export class CreateUserDto {
  @IsString()
  public username: string;

  @IsEmail()
  public email?: string;

  @IsString()
  public password: string;

  @IsString()
  public nickname: string;
}

export class LoginUserDto {
  @IsString()
  public username: string;

  @IsString()
  public password: string;
}
